import { CSSProperties } from "react";
import {
  JSONNode,
  ReactNodeData,
  NodeType,
  EntityNode,
} from "../common/common-types";

import { MarkerType, EdgeMarker } from "reactflow";

function getDescription(node_data: any) {
  const text_fields = ["description", "section", "paragraph", "concept"];
  for (let i = 0; i < text_fields.length; i++) {
    const field = text_fields[i];
    if (node_data[field]) {
      return node_data[field];
    }
  }
  return "Error, but lets ignore for now";
}

type ReactFlowNode = {
  id: string;
  data: ReactNodeData;
  position: {
    x: number;
    y: number;
  };
  type: NodeType;
  hidden: boolean;
};

type ReactFlowEdge = {
  id: string;
  source: string;
  target: string;
  style?: CSSProperties;
  markerEnd: EdgeMarker;
  data?: {
    label?: string;
  };
};

export const processJson = (
  json: JSONNode | null,
): { initialNodes: any[]; initialEdges: any[] } => {
  const initialNodes: ReactFlowNode[] = [];
  const initialEdges: ReactFlowEdge[] = [];

  // return empty nodes and edges
  if (!json) {
    return { initialNodes, initialEdges };
  }

  const traverse = (node: JSONNode, parentId?: string) => {
    // Add the node
    const parentNode = {
      id: node.id,
      data: {
        label: node.node_data.title,
        description: getDescription(node.node_data),
        // TODO: id is repeated here but kinda need it for the custom node which only takes
        // the data argument
        id: node.id,
        color: node.node_data.color ? node.node_data.color : "#3F301D",
      },
      position: {
        x: 0,
        y: 0,
      },
      type:
        node.node_data.node_type === "ROOT"
          ? NodeType.AttachedNode
          : NodeType.UnattachedNode,
      // hidden: false
      hidden: node.show ? false : true,
    };

    initialNodes.push(parentNode);
    // Add an edge if there is a parent ID
    if (parentId) {
      initialEdges.push({
        id: `e${parentId}-${node.id}`,
        source: parentId,
        target: node.id,
        markerEnd: {
          type: MarkerType.Arrow,
        },
        style: {
          stroke: parentNode.data.color,
          strokeDasharray: "5,5",
          strokeWidth: 4,
        },
      });
    }

    // Recursively process children if any
    if (node.node_data.children) {
      node.node_data.children.forEach((child) => traverse(child, node.id));
    }
  };

  traverse(json);
  return { initialNodes, initialEdges };
};

function generateRandomString(length = 4) {
  const characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  let result = "";

  for (let i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * characters.length));
  }

  return result;
}

export const processJsonEntities = (
  json: JSONNode,
): { initialNodes: any[]; initialEdges: any[] } => {
  const initialNodes: ReactFlowNode[] = [];
  const initialEdges: ReactFlowEdge[] = [];

  const isInNodesArray = (label: string) => {
    return initialNodes.find((node) => node.data.label === label);
  };

  const traverse = (node: JSONNode, parentId?: string) => {
    const addNode = (label: string) => {
      const node_id = generateRandomString();
      initialNodes.push({
        id: node_id,
        data: {
          label: label,
          id: node_id,
          color: node.node_data.color ? node.node_data.color : "#3F301D",
          description: "",
        },
        position: {
          x: 0,
          y: 0,
        },
        hidden: false,
        type: NodeType.AttachedNode,
      });
      return node_id;
    };
    const addEdge = (source_id: string, target_id: string, label: string) => {
      initialEdges.push({
        id: `e${source_id}-${target_id}`,
        source: source_id,
        target: target_id,
        data: {
          label: label,
        },
        markerEnd: {
          type: MarkerType.Arrow,
        },
        style: {
          stroke: node.node_data.color,
          strokeDasharray: "5,5",
          strokeWidth: 4,
        },
      });
    };

    if (node.node_data.entity_relations) {
      // only add entity relations
      node.node_data.entity_relations.forEach((entity: EntityNode) => {
        const {
          target: target_label,
          source: source_label,
          relation: relation_label,
        } = entity;

        const targetNode = isInNodesArray(target_label);
        const sourceNode = isInNodesArray(source_label);

        if (!targetNode && !sourceNode) {
          const target_nodeid = addNode(target_label);
          const source_nodeid = addNode(source_label);
          addEdge(source_nodeid, target_nodeid, relation_label);
        } else if (!targetNode && sourceNode) {
          const target_nodeid = addNode(target_label);
          addEdge(sourceNode.id, target_nodeid, relation_label);
        } else if (targetNode && !sourceNode) {
          const source_nodeid = addNode(source_label);
          addEdge(source_nodeid, targetNode.id, relation_label);
        } else {
          console.log("Im am adding edge betewen existing");
          addEdge(sourceNode.id, targetNode.id, relation_label);
        }
      });

      // Recursively process children if any
      if (node.node_data.children) {
        node.node_data.children.forEach((child) => traverse(child, node.id));
      }
    }
  };

  traverse(json);
  return { initialNodes, initialEdges };
};
