import React, { useState, useEffect, useMemo } from "react";
import ReactFlow, {
  ReactFlowProvider,
  Panel,
  useNodesState,
  useEdgesState,
  applyNodeChanges,
  NodeChange,
  EdgeChange,
  OnNodesChange,
  applyEdgeChanges,
  OnEdgesChange,
} from "reactflow";

import { GRAPH_ENDPOINT } from "../api/constants";

import { useContext } from "use-context-selector";
import { GlobalContext, GlobalProvider } from "./provider/globalProvider";

// import { test_node } from './conversations/test.js';
// import { raypeat_med } from './conversations/raypeat_med.js';
// import { small_test } from './conversations/test_small.js';
import { russia } from "./conversations/russia.js";
// import { entity_relations } from './conversations/entity_relations';

import { processJson } from "./data/processJson";
// import { processJsonEntities } from './data/processJson';
// import {raypeat} from './conversations/raypeat.js';

import AttachedNode from "./components/node/AttachedNode";
import UnattachedNode from "./components/node/UnattachedNode";
import useForceLayout from "./layout/useForceLayout";

import { JSONNode, getColors } from "./common/common-types";
import { useParams } from "react-router-dom";

import axios from "axios";

import "reactflow/dist/style.css";

// let {initialEdges, initialNodes} = processJsonEntities(entity_relations);

const nodeTypes = {
  attachedNode: AttachedNode,
  unattachedNode: UnattachedNode,
};

const ConceptMapPage = () => {
  let initialNodes = [];
  let initialEdges = [];

  const { mapId } = useParams();
  // const [dataFetched, setDataFetched] = useState(false);

  const [nodes, setNodes] = useNodesState([]);
  const [edges, setEdges] = useEdgesState([]);

  useEffect(() => {
    async function fetchData() {
      try {
        const response = await axios.get(GRAPH_ENDPOINT(mapId));
        const result = processJson(response.data);

        initialNodes = result.initialNodes;
        initialEdges = result.initialEdges;

        setNodes(initialNodes);
        setEdges(initialEdges);
        // setDataFetched(true);  // set dataFetched to true after successfully setting graphJSON
      } catch (error) {
        console.error("Error fetching graph:", error);
      }
    }

    fetchData();
  }, [mapId, setNodes, setEdges]);

  const [initialized, setInitialized] = useState<boolean>(false);

  // useAutoLayout({direction:"TB"});
  useForceLayout({ strength: -1, distance: 150 });

  const { setSelectedNode } = useContext(GlobalContext);
  if (!initialized && initialNodes.length > 0) {
    const rootNode = nodes[0];
    setSelectedNode(rootNode.id);

    const outEdges = edges.filter((edge) => edge.source === rootNode.id);
    const topLevelParents = nodes.filter((node) => {
      return outEdges.some((edge) => edge.target === node.id);
    });

    // give top level parent random color
    const colors = getColors(topLevelParents.length);
    setNodes(
      nodes.map((node) => {
        if (topLevelParents.some((parent) => parent.id === node.id)) {
          return {
            ...node,
            data: {
              ...node.data,
              color: colors.pop(),
            },
          };
        } else {
          return node;
        }
      })
    );
  }

  const onNodesChange: OnNodesChange = (changes: NodeChange[]) => {
    setNodes((nodes) => applyNodeChanges(changes, nodes));
  };

  const onEdgesChange: OnEdgesChange = (changes: EdgeChange[]) => {
    setEdges((edges) => applyEdgeChanges(changes, edges));
  };

  if (!initialized) setInitialized(true);

  return (
    <ReactFlow
      nodes={nodes}
      edges={edges}
      onNodesChange={onNodesChange}
      onEdgesChange={onEdgesChange}
      nodeTypes={nodeTypes}
      fitView
    >
      <Panel position="top-right">
        {/* <button onClick={onLayout}>layout</button> */}
      </Panel>
    </ReactFlow>
  );
};

// export default ConceptMapPage;

export default function () {
  return (
    <ReactFlowProvider>
      <GlobalProvider reactFlowWrapper={"hello"}>
        <ConceptMapPage />
      </GlobalProvider>
    </ReactFlowProvider>
  );
}
