// basically typescript enums
export type SchemaId = string & { readonly __schemaId: never };
export type InputId = number & { readonly __inputId: never };

export type Mutable<T> = { -readonly [P in keyof T]: T[P] };

export type InputValue = InputSchemaValue | undefined;
export type InputSchemaValue = string | number;

export type JSONNode = {
  id: string;
  attached?: boolean;
  show?: boolean;
  node_data: {
    title: string;
    node_type: string;
    children?: JSONNode[];
    concept?: string;
    description?: string;
    color?: string;
    entity_relations?: EntityNode[];
  };
};

export type EntityNode = {
  source: string;
  target: string;
  relation: string;
};

export type ReactNodeData = {
  id: string;
  label: string;
  description: string;
  color?: string;
};

export enum NodeType {
  AttachedNode = "attachedNode",
  UnattachedNode = "unattachedNode",
}

const COLORS = [
  "#F18F01",
  "#99C24D",
  "#77A6B6",
  "#D81159",
  "#8A4FFF",
  "#EDAFB8",
  "#56EEF4",
];

function getRandomElements(arr, n) {
  // Fisher-Yates shuffle
  for (let i = arr.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [arr[i], arr[j]] = [arr[j], arr[i]];
  }
  return arr.slice(0, n);
}

export function getColors(n: number) {
  return getRandomElements(COLORS, n);
}

// why is this here
export interface NodeData {
  readonly id: string;
  readonly parentNode?: string;
  readonly minWidth?: number;
  readonly minHeight?: number;
  readonly schemaId: SchemaId;
  readonly title: string;
  readonly description?: string;
}

export interface EdgeData {
  sourceX?: number;
  sourceY?: number;
  targetX?: number;
  targetY?: number;
}

export type InputData = Readonly<Record<InputId, InputValue>>;

